.aboutme {display : flex;flex-direction : column;
  &_window {align-content : center;align-items : center;display : flex;flex-basis : var(--screen-height);justify-content : center;
    @media #{$break1}{}
    @media #{$break2}{}
  }
  //WHO AM I ?
  &_topic {align-items : baseline;color : var(--color-text-light);display : flex;flex-wrap : wrap;font-family : "Raleway", sans-serif;font-weight : 300;
    @media #{$break1}{align-items : center;flex-direction : column;font-size : 10rem;justify-content : space-evenly;}
    @media #{$break2} {column-gap : 20rem;font-size : 10rem;}
    & .avatar {clip-path : circle(50px at 50px 50px);height : 100px;width : 100px;}
  }
  //CARDS
  &_cards {align-items : center;align-self : center;background-color : var(--color7-light);border-radius : 4rem;display : flex;font-family : "Raleway", sans-serfif;
    @media#{$break1}{flex-direction : column;justify-content : space-evenly;}
    @media#{$break2}{flex-basis : 80%;flex-direction : row;height : calc(var(--screen-height) * 0.6);justify-content : space-between;padding-left: 5rem;}
    &_selectors {color : var(--color5-normal);display : flex;font-size : 3rem;font-weight : 400;
      & button {cursor : pointer;white-space : nowrap;}
      @media #{$break1}{flex-direction : row;justify-content : space-evenly;}
      @media #{$break2}{flex-basis : 15%;flex-direction : column;justify-content : center;row-gap : 5rem;}
    }
    @media #{$break1} {}
    & > .card {background-color : var(--color5-extra-light);border-radius : inherit;box-shadow : var(--color6-normal) 0px 0px 20px 0px;display : flex;flex-direction : column;flex-wrap : nowrap;padding : 10rem;row-gap : 2rem;
      @media #{$break2} {align-content : center;align-self : stretch;flex-basis : 80%;flex-direction : column;justify-content : center;}
      & h3 {color : var(--color-text-extra-light);font-family : "Rubik", sans-serif;font-weight : 400;
        @media #{$break1} {}
        @media #{$break2} {font-size : 9rem;}
      }
      & > .paragraph {font-family : "Rubik", sans-serif;font-weight : 100;
        @media #{$break1} {}
        @media #{$break2} {font-size : 3.3rem;font-weight : 100}
      }
      & .coding_card {display : flex;row-gap : 4rem;
        @media #{$break1} {}
        @media #{$break2} {flex-direction : row;flex-wrap : wrap;justify-content : space-evenly;}
        & > p {font-family : "Rubik", sans-serif;font-weight : 100;
          @media #{$break1} {}
          @media #{$break2} {font-size : 3.3rem;}
        }
        & a {border : 1px solid var(--color6-normal);border-radius : 6rem;column-gap : 1rem;display : flex;font-size : 3.3rem;padding : 3rem;}
      }
    }
  }
}
