.greeting {align-items : center;display : flex;flex-basis : var(--screen-height);font-weight : 900;width : var(--screen-width);
  @media #{$break1} {flex-direction : column;justify-content : space-evenly;}
  @media #{$break2} {justify-content : space-evenly;justify-content : space-evenly;
  }
  &_text {color : var(--color-text-normal);display : inline-block;font-family : "Raleway", sans-serif;font-size : 10rem;@include slide-right-animation(var(--animation-delay-400), var(--animation-duration-1100));line-height : 200%;
    & .hand-wave_icon {display : inline-block;@include hand-wave-animation(var(--animation-delay-400), var(--animation-duration-900));
      &:hover {@include hand-wave-animation(var(--animation-delay-400), var(--animation-duration-900));}
    }
    @media #{$break1}{width : 80%;}
  }
  & .connect_section {align-self : flex-end;display : flex;justify-content : center;
    @media #{$break1} {}
    @media #{$break2} {color : var(--color-text-extra-light);column-gap : 4rem;font-size : 4rem;margin-bottom: 5rem;}
  }
  & .connect_button {color : var(--color-text-light);font-family : "Raleway", sans-serif;font-size : 4rem;font-weight : 100;}
}
