:root {
  --border-radius1 : 8rem;
}
.slider-button {
  &_wrapper {background-image : linear-gradient(120deg, var(--color2-dark), var(--color4-normal));border-radius : var(--border-radius1);padding : .1rem;scale : min(calc(var(--scroll-y) / 1000 + 1), 1.1);@include blur-in-animation(0ms, var(--animation-duration-1100));}
  &_text {align-items : center; align-self : center;background : var(--color6-normal);border-radius : var(--border-radius1);cursor : pointer;display : flex;padding : 0rem 3rem;@include slide-up-animation(var(--animation-delay-700), var(--animation-duration-600));}
  &:hover{
    &_content {transform : scale(1.1);}
  }
}







.button_common2 {
  --border-radius1 : 50%;
  &_border {background-image : linear-gradient(120deg, var(--color2-dark), var(--color4-normal));border-radius : 50%;padding : .1rem;scale : min(calc(var(--scroll-y) / 1000 + 1), 1.1);@include blur-in-animation(0ms, var(--animation-duration-1100));
    &:hover {
      //@include gradient-border-flush-animation(var(--animation-delay-100), var(--animation-duration-600));
    }
  }
  &_content {
    align-items : center; align-self : center;background : var(--color6-normal);border-radius : var(--border-radius1);cursor : pointer;display : flex;padding : 3rem 3rem;
    @include slide-up-animation(var(--animation-delay-700), var(--animation-duration-600));
  }
}